const menuLinks = document.querySelectorAll(".menu ul li a");
const menuList = document.querySelector(".menu__list");
const checkbox = document.querySelector('input[type="checkbox"]');

menuLinks.forEach(function (link) {
  link.addEventListener("click", function () {
    menuList.style.right = "0";
    menuList.style.visibility = "visibile";
    checkbox.checked = !checkbox.checked;
  });
});
const swiper = new Swiper(".swiper", {
  // Optional parameters
  loop: true,
  effect: "coverflow",
  grabCursor: true,
  centeredSlides: true,
  slidesPerView: "auto",
  coverflowEffect: {
    rotate: 40,
    stretch: 0,
    depth: 100,
    modifier: 1,
    slideShadows: true,
  },
  // Navigation arrows
  navigation: {
    nextEl: ".swiper-button-next",
    prevEl: ".swiper-button-prev",
  },
});

function calculate() {
  const numberInput = document.getElementById("numberInput");
  const planSelect = document.getElementById("planSelect");
  const phoneInput = document.getElementById("phoneInput");
  const resultDiv = document.getElementById("result");

  const number = Number(numberInput.value);
  const plan = Number(planSelect.value);
  const phone = phoneInput.value.trim();

  if (isNaN(number)) {
    resultDiv.innerText = "Будь ласка, введіть число";
    return;
  }

  if (!phone) {
    resultDiv.innerText = "Будь ласка, введіть номер телефону";
    return;
  }

  const result = number * plan;
  resultDiv.innerText = `Приблизна початкова вартість Вашої тераси становить від ${result} $`;

  clearForm();
}

function clearForm() {
  const form = document.getElementById("formCalc");
  form.reset();
}
